export function title(title) {
    if (!(title.value.trim().length > 3)) {
        return 'Ttulo muito pequeno.';
    }
    else {
        return null;
    }
}
export function desc(desc) {
    if (!(desc.value.trim().length > 3)) {
        return 'Descrio muito pequena.';
    }
    else {
        return null;
    }
}
